.hero {
  color: white;
  display: flex;
  justify-content: center;
  min-height: 100vh;
  height: 100vh;
}
.hero-content {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  gap: 1rem;
}
.hero-text {
  font-size: 1.25rem;
  font-weight: 700;
  animation-name: zoomIn;
  animation-duration: 1s;
}
.hero-circle {
  width: 240px;
  height: 240px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  position: relative;
}
.hero-circle-animation {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  border: 2px dashed rgba(255, 255, 255, 0.4);
  animation-name: zoomIn, circleAround;
  animation-duration: 1s, 10s;
  animation-delay: 0s, 0.5s;
  animation-iteration-count: 1, infinite;
  animation-timing-function: linear, linear;
  animation-direction: normal, reverse;
}
.hero-img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 220px;
  height: 220px;
  border-radius: 50%;
  border: 1px solid black;
  background-color: white;
  overflow: hidden;
}
.hero-img img {
  animation-name: zoomIn;
  animation-duration: 1s;
  width: 100%;
}

